@import "./vars.scss";

.contact {
  text-align: center;
  padding: 3rem 0;

  &__title {
    font-size: 2rem;
    display: block;
    padding-bottom: 0.5rem;
    text-align: center;
    font-weight: 100;
    text-transform: uppercase;
  }

  &__heading {
    font-size: 1.3rem;
    margin-bottom: 2rem;
  }

  .contact-form {
    display: grid;
    grid-template-areas: "name email"
      "subject phone"
      "message message";
    grid-gap: 1.2rem;
    margin-bottom: 1.2rem;

    .contact-netify {
      margin: 2rem 0;

    }

    &__input {
      padding: 0.5rem 1rem;

      &--name {
        grid-area: name;
      }

      &--subject {
        grid-area: subject;
      }

      &--email {
        grid-area: email;
      }

      &--phone {
        grid-area: phone;
      }

      &--message {
        grid-area: message;
        height: 100px;
      }
    }
  }
  button[type="submit"] {
    width: 50%;
  }
}

.contact-info {
  background: $dark-color;
  padding: 3rem 0;

  &__about {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;

    &--font {
      color: white;
    }
  }
}

.contact-start {
  padding: 4rem 0;
  background: $main-color;

  &__info {
    text-align: center;
    font-size: 4rem;
  }
}
